/** Switch
-------------------------------------*/

.toggle {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  width: 3.2291666666666665vw;
  height: 1.6666666666666667vw;
  display: inline-block;
  position: relative;
  border-radius: 50px;
  overflow: hidden;
  outline: none;
  border: none;
  cursor: pointer;
  background-color: #ddd;
  transition: background-color ease 0.3s;
  /*border : solid red 2px;*/
}

.absoluteView {
  background-color: #2e3e97;
  border-bottom-left-radius: 30px;
  border-bottom-right-radius: 30px;
  padding-left: 1.3020833333333333vw;
  padding-right: 1.3020833333333333vw;
  padding-bottom: 0.2604166666666667vw;
  padding-top: 0.5208333333333334vw;
}

.absoluteView h2 {
  font-size: 1.6666666666666667vw;
  line-height: 1.8229166666666667vw;
  margin: 0.5208333333333334vw 0;
  font-weight: 400;
}

.toggle:before {
  content: "";
  display: block;
  position: absolute;
  z-index: 2;
  width: 1.4583333333333333vw;
  height: 1.4583333333333333vw;
  background: #fff;
  left: 0.10416666666666667vw;
  top: 0.10416666666666667vw;
  border-radius: 50%;
  font: 10px/28px Helvetica;
  text-transform: uppercase;
  font-weight: bold;
  text-indent: -22px;
  word-spacing: 37px;
  color: #fff;
  text-shadow: -1px -1px rgba(0, 0, 0, 0.15);
  white-space: nowrap;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
  transition: all cubic-bezier(0.3, 1.5, 0.7, 1) 0.3s;
}

.toggle:checked {
  background-color: #4CD964;
}

.toggle:checked:before {
  left: 32px;
}



